import PropTypes from 'prop-types';
import Grid from '@mui/material/Grid';
import { ProfileAvatar } from 'components/profile';
import Thumbnail from 'pages/Thumbnail';

import LoadMore from 'pages/LoadMore';

export default function ItemGrid({
  items,
  handleMoreClick,
  nextToken,
  handleImageLoad,
  createComponent,
  handleClick,
}) {
  const getThumbnailSrcSet = (thumbnailImage) => {
    const srcSetList = thumbnailImage.images
      ? thumbnailImage.images.map(({ src, width }) => `${src} ${width}w`)
      : [];
    return srcSetList.join(',');
  };

  return (
    <Grid
      container
      sx={{ bgcolor: 'button.main', py: 1 }}
      justifyContent="left"
      alignItems="flex-start"
    >
      {createComponent}
      {items.map((item) => (
        <Grid item key={item.title} lg={3} md={4} sm={6} xs={12} sx={{ p: 1 }}>
          <Thumbnail
            title={item.title}
            date={item.date}
            avatar={
              item.owner ? (
                <ProfileAvatar
                  name={item.owner.name}
                  picture={item.owner.picture}
                  email={item.owner.email}
                />
              ) : null
            }
            shared={item.shared}
            thumbnailImage={item.photo.src}
            thumbnailImageSrcSet={getThumbnailSrcSet(item.photo)}
            handleClick={() => handleClick(item)}
            handleImageLoad={handleImageLoad}
          />
        </Grid>
      ))}
      {nextToken && <LoadMore handleMoreClick={handleMoreClick} />}
    </Grid>
  );
}

ItemGrid.defaultProps = {
  items: [],
  createComponent: '',
};

ItemGrid.propTypes = {
  createComponent: PropTypes.node,
  items: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      photo: PropTypes.arrayOf(
        PropTypes.shape({
          src: PropTypes.string,
          title: PropTypes.string,
        })
      ),
    })
  ),
  nextToken: PropTypes.string.isRequired,
  handleMoreClick: PropTypes.func.isRequired,
  handleImageLoad: PropTypes.func.isRequired,
  handleClick: PropTypes.func.isRequired,
};
