import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { ReactComponent as Logo } from 'static/images/icons/logo.svg';

export default function NoMemories({ message }) {
  const navigate = useNavigate();
  const handleClick = () => navigate('/memory/create');

  return (
    <Stack
      direction="column"
      justifyContent="center"
      alignItems="center"
      sx={{
        width: '95%',
        maxWidth: '500px',
        position: 'fixed',
        top: '50%',
        right: '50%',
        transform: 'translate(50%, -50%)',
        border: '2px solid gray',
        height: '60%',
        borderRadius: '10px',
      }}
    >
      <Logo fill="gray" width="64px" height="64px" />
      <Typography color="gray" sx={{ fontSize: '1.3rem' }}>
        {message}
      </Typography>
      <Typography color="gray" sx={{ pb: 8, fontSize: '1.3rem' }}>
        Check again later.
      </Typography>
      <Typography color="primary">In the mean time, you can</Typography>
      <Button variant="contained" onClick={handleClick}>
        Create a Memory
      </Button>
    </Stack>
  );
}

NoMemories.propTypes = {
  message: PropTypes.string.isRequired,
};
