import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import ItemLoading from 'pages/ItemLoading';
import SkeletonGrid from 'pages/SkeletonGrid';
import NoMemories from 'components/NoMemories';
import memoryapi from 'api/memory';
import ItemGrid from 'pages/ItemGrid';

async function getAcceptedMemories(
  handleMemoryCallback,
  handleTokenCallback,
  handleThumbnailCallback,
  nextToken = ''
) {
  try {
    const resp = await memoryapi.getAcceptedMemories(nextToken);
    if (resp) {
      if (resp.memories) {
        handleThumbnailCallback(resp.memories);
        const thumbnails = await Promise.all(
          memoryapi.getAcceptedThumbnails(resp.memories)
        );
        handleMemoryCallback(thumbnails);
        handleTokenCallback(resp.next_continuation_token || '');
      }
    }
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function SharedMemories() {
  const [nextToken, setNextToken] = useState('');
  const [memoryIDs, setMemoryIDs] = useState([]);
  const [memories, setMemories] = useState([]);
  const [count, setCount] = useState(0);
  const [grid, setGrid] = useState(null);
  const navigate = useNavigate();

  const handleClick = (memDetails) => {
    navigate(`/memory/shared/${memDetails.memory_id}`, { state: memDetails });
  };

  const picLoaded = () => setCount((cnt) => cnt + 1);
  const handleTokenChange = (newToken) => setNextToken(newToken);

  const addMemories = (details) => {
    details.map((detail) => {
      const memDetails = detail.thumbnail;
      memDetails.owner = detail.owner_details;
      setMemories((arr) => arr.concat([memDetails]));
      return 0;
    });
  };

  const addMemoryIDs = (ids) => setMemoryIDs((arr) => arr.concat(ids));

  const sortedMemories = memories.sort((a, b) =>
    a.memory_id > b.memory_id ? 1 : -1
  );

  const handleMoreClick = () =>
    getAcceptedMemories(
      addMemories,
      handleTokenChange,
      addMemoryIDs,
      nextToken
    );

  useEffect(
    () =>
      getAcceptedMemories(
        addMemories,
        handleTokenChange,
        addMemoryIDs,
        nextToken
      ),
    []
  );

  function GetThumbnailGrid() {
    if (memories.length > 0) {
      const thumbnailGrid = (
        <ItemGrid
          handleClick={handleClick}
          handleImageLoad={picLoaded}
          handleMoreClick={handleMoreClick}
          items={sortedMemories}
          nextToken={nextToken}
        />
      );
      setGrid(thumbnailGrid);
    } else {
      setGrid(<NoMemories message="No shared memories right now." />);
    }
  }

  useEffect(() => GetThumbnailGrid(), [memories, nextToken]);

  return memories.length === memoryIDs.length && count >= memoryIDs.length ? (
    grid
  ) : (
    <>
      <SkeletonGrid>
        {memoryIDs.map((memory) => (
          <ItemLoading memory={memory} />
        ))}
      </SkeletonGrid>

      <Box component="div" sx={{ display: 'none' }}>
        {grid}
      </Box>
    </>
  );
}
