import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import ActionButton from 'components/ActionButton';
import OwnerInfo from 'components/OwnerInfo';
import SimpleSnackbar from 'components/simple_snackbar';
import Skeleton from '@mui/material/Skeleton';
import imageLoading from 'static/images/image-loading.png';
import commonUtils from 'utils';
import NoMemories from 'components/NoMemories';
import ReviewItem from 'components/ReviewItem';

import memoryapi from 'api/memory';

const getThumbnailSrcSet = (thumbnailImage) => {
  const srcSetList = thumbnailImage.images
    ? thumbnailImage.images.map(({ src, width }) => `${src} ${width}w`)
    : [];
  return srcSetList.join(',');
};

async function getReviewMemory(
  handleThumbnailCallback,
  handleMemoryCallback,
  handleCountCallback,
  memoryID = ''
) {
  try {
    const resp = await memoryapi.getPendingMemories(memoryID);
    if (resp) {
      handleCountCallback(resp.count);
      if (resp.memory) {
        handleMemoryCallback(resp.memory);
        const thumbnail = await memoryapi.getPendingThumbnails(resp.memory);
        handleThumbnailCallback(thumbnail);
      }
    }
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

async function acceptMemory(memoryID, handleAcceptCallback) {
  try {
    const userInfo = commonUtils.getUserInfo();
    const resp = await memoryapi.acceptShare(memoryID, userInfo.email);
    handleAcceptCallback(resp);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

async function rejectMemory(memoryID, handleRejectCallback) {
  try {
    const userInfo = commonUtils.getUserInfo();
    const resp = await memoryapi.rejectShare(memoryID, userInfo.email);
    handleRejectCallback(resp);
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function ReviewMemories({ memoryID }) {
  const [count, setCount] = useState(null);
  const [loaded, setLoaded] = useState(false);
  const [, setMemID] = useState(null);
  const [pendingMemory, setPendingMemory] = useState(null);
  const [grid, setGrid] = useState(null);

  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState(null);
  const [snackError, setSnackError] = useState(null);
  const [accepting, setAccepting] = useState(false);
  const [rejecting, setRejecting] = useState(false);
  const [message, setMessage] = useState(null);

  const startAccepting = () => {
    setMessage('Accepting memory. Please wait...');
    setAccepting(true);
  };

  const startRejecting = () => {
    setMessage('Rejecting memory. Please wait...');
    setRejecting(true);
  };

  const endRunning = () => {
    setMessage(null);
    setAccepting(false);
    setRejecting(false);
  };

  const handleSnackMessage = (msg, error) => {
    setSnack(true);
    setSnackMessage(msg);
    setSnackError(error);
  };

  const handleSnackBarClose = () => {
    setSnack(false);
    setSnackMessage(null);
    setSnackError(null);
  };

  const handleMemoryRemove = () => {
    setPendingMemory(null);
    setGrid(null);
    getReviewMemory(setPendingMemory, setMemID, setCount, memoryID);
  };

  useEffect(
    () => getReviewMemory(setPendingMemory, setMemID, setCount, memoryID),
    []
  );

  const handleAcceptActionResults = (resp) => {
    if (resp.ok) {
      handleSnackMessage('Memory share accepted', false);
      setLoaded(false);
      handleMemoryRemove();
    } else {
      handleSnackMessage('Error while accepting memory share', true);
    }
  };

  const handleRejectActionResults = (resp) => {
    if (resp.ok) {
      handleSnackMessage('Memory share rejected', false);
      setLoaded(false);
      handleMemoryRemove();
    } else {
      handleSnackMessage('Error while rejecting memory share', true);
    }
  };

  const handleAccept = () => {
    startAccepting();
    acceptMemory(pendingMemory.thumbnail.memory_id, handleAcceptActionResults);
    endRunning();
  };

  const handleReject = () => {
    startRejecting();
    rejectMemory(pendingMemory.thumbnail.memory_id, handleRejectActionResults);
    endRunning();
  };

  const handleImageLoad = () => setLoaded(true);

  if (count === 0 && grid === null) {
    setGrid(<NoMemories message="Nothing to review right now." />);
  }

  function ReviewCarousel() {
    if (pendingMemory !== null) {
      const rc = (
        <Box
          sx={{
            display: 'flex',
            width: '100%',
            backgroundColor: 'transparent',
            backgroundSize: 'cover',
            backgroundImage: 'linear-gradient(white, #0e516a)',
            justifyContent: 'center',
          }}
        >
          <Stack
            direction="column"
            alignItems="center"
            sx={{ width: '100%', my: 3 }}
          >
            <OwnerInfo
              name={pendingMemory.owner_details.name || ''}
              email={pendingMemory.owner_details.email || 'P'}
              picture={pendingMemory.owner_details.picture || ''}
            />
            <Typography sx={{ mb: 2 }}>has shared a memory with you</Typography>
            <Stack
              key={pendingMemory.thumbnail.memory_id}
              direction="column"
              spacing={1}
              alignItems="center"
            >
              <ReviewItem
                title={pendingMemory.thumbnail.title}
                date={pendingMemory.thumbnail.date}
                thumbnailImage={pendingMemory.thumbnail.photo.src}
                thumbnailImageSrcSet={getThumbnailSrcSet(
                  pendingMemory.thumbnail.photo
                )}
                handleImageLoad={handleImageLoad}
                handleClick={() => {}}
              />
            </Stack>
            <Stack direction="row" sx={{ m: 1 }}>
              <ActionButton
                label="Accept"
                color="primary"
                handleClick={handleAccept}
                disabled={accepting || rejecting}
              />
              <ActionButton
                label="Reject"
                color="error"
                handleClick={handleReject}
                disabled={accepting || rejecting}
              />
            </Stack>
          </Stack>
        </Box>
      );
      setGrid(rc);
    }
  }

  useEffect(
    () => ReviewCarousel(),
    [pendingMemory, memoryID, accepting, rejecting]
  );

  return (pendingMemory !== null && loaded) || count === 0 ? (
    <>
      {accepting === true || rejecting === true ? (
        <Typography variant="h6" sx={{ p: 2, color: 'white' }}>
          {message}
        </Typography>
      ) : null}
      {grid}
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
        error={snackError}
      />
    </>
  ) : (
    <>
      <Box
        sx={{
          display: 'flex',
          width: '100%',
          backgroundColor: 'transparent',
          backgroundSize: 'cover',
          backgroundImage: 'linear-gradient(white, #0e516a)',
          justifyContent: 'center',
        }}
      >
        <Stack
          direction="column"
          alignItems="center"
          sx={{ width: '100%', my: 3 }}
        >
          <OwnerInfo
            name={<Skeleton type="rectangular" sx={{ width: '35%' }} />}
            email="P"
            picture=""
          />
          <Typography sx={{ mb: 2 }}>has shared a memory with you</Typography>
          <Stack
            key="loading"
            direction="column"
            spacing={1}
            alignItems="center"
          >
            <ReviewItem
              title=""
              date=""
              thumbnailImage={imageLoading}
              thumbnailImageSrcSet=""
              handleImageLoad={() => {}}
              handleClick={() => {}}
            />
          </Stack>
          <Stack direction="row" sx={{ m: 1 }}>
            <ActionButton
              label="Accept"
              color="primary"
              handleClick={() => {}}
              disabled
            />
            <ActionButton
              label="Reject"
              color="error"
              handleClick={() => {}}
              disabled
            />
          </Stack>
        </Stack>
      </Box>
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
        error={snackError}
      />
      <Box component="div" sx={{ display: 'none' }}>
        {grid}
      </Box>
    </>
  );
}

ReviewMemories.defaultProps = {
  memoryID: '',
};

ReviewMemories.propTypes = {
  memoryID: PropTypes.string,
};
