import PropTypes from 'prop-types';
import useMediaQuery from '@mui/material/useMediaQuery';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';

import MemoryThumbnailCard from 'pages/ThumbnailCard';

export default function CreateMemoryItem({ message, handleClick }) {
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));

  return (
    <Grid item key="create" lg={3} md={4} sm={6} xs={12} sx={{ p: 1 }}>
      <MemoryThumbnailCard handleClick={handleClick}>
        <Typography
          variant={belowmd ? 'body1' : 'h5'}
          color="button.main"
          sx={{
            p: 1,
            color: 'secondary.main',
            bgcolor: 'primary.main',
            borderRadius: '0px 10px 10px 0px',
            borderColor: 'secondary.main',
            position: 'absolute',
            bottom: 15,
            left: 0,
          }}
        >
          {message}
        </Typography>
        <AddOutlinedIcon
          sx={{
            position: 'absolute',
            border: 3,
            borderRadius: '50%',
            fontSize: '7.5rem',
            left: '50%',
            top: '50%',
            transform: 'translate(-50%, -50%)',
            color: 'muted.main',
          }}
        />
      </MemoryThumbnailCard>
    </Grid>
  );
}

CreateMemoryItem.propTypes = {
  message: PropTypes.string.isRequired,
  handleClick: PropTypes.func.isRequired,
};
